.about__info-inner .icon {
  margin-left: 4px;
  fill: #fff;
  vertical-align: middle;
  font-size: 18px;
  transition: transform .3s;
}

.popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 6;
  padding: 30px;
  visibility: hidden;
  overflow: auto;
  text-align: center;
  font-size: 0;
}

.popup:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.popup::-webkit-scrollbar {
  width: 5px;
}

.popup::-webkit-scrollbar-thumb {
  background-color: #3D3A41;
}

.popup.is-active {
  visibility: visible;
}

.popup__overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  opacity: 0;
  background-color: rgba(25, 25, 38, 0.3);
}

.popup__inner {
  position: relative;
  width: 100%;
  max-width: 1200px;
  overflow: hidden;
  vertical-align: middle;
  display: inline-block;
}

.popup__close {
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  color: #fff;
  font-size: 0;
  z-index: 1;
  opacity: 0;
  background-color: #191926;
}

.popup__close .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -9px 0 0 -9px;
  font-size: 18px;
}

.popup__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  transform: scaleY(0);
}

.popup__triangle {
  position: absolute;
  top: 60%;
  left: 51.5%;
  opacity: 0.2;
  stroke-dasharray: 2500;
  stroke-dashoffset: 2500;
}

.popup__triangle-blur {
  top: 50%;
  left: 59%;
  filter: blur(8px);
}

.popup__triangle-blur svg {
  transform: rotate(180deg);
  opacity: 0;
  visibility: hidden;
  transition: opacity .7s, visibility .7s;
}

.popup__triangle-blur svg.show-blur {
  visibility: visible;
  opacity: 1;
}

.popup__triangle, .popup__triangle-blur {
  position: absolute;
  transform: translate(-50%, -50%);
}

.popup__content {
  position: relative;
  padding: 100px 50px 180px;
  padding-top: 70px;
  padding-top: calc(20px + (70 - 20) * ((100vw - 1024px) / (1440 - 1024)));
  padding-bottom: 100px;
  padding-bottom: calc(20px + (100 - 20) * ((100vw - 1024px) / (1440 - 1024)));
  color: #3D3A41;
  font-size: 20px;
}

@media (max-width: 1024px) {
  .popup__content {
    padding-top: 20px;
  }
}

@media (min-width: 1440px) {
  .popup__content {
    padding-top: 70px;
  }
}

@media (max-width: 1024px) {
  .popup__content {
    padding-bottom: 20px;
  }
}

@media (min-width: 1440px) {
  .popup__content {
    padding-bottom: 100px;
  }
}

.popup__title {
  font-family: "FuturaFuturis", Open Sans, sans-serif;
  font-size: 30px;
  font-weight: 300;
  line-height: 1.6;
  color: #3D3A41;
  letter-spacing: 0;
}

.popup__email {
  color: #ACA1B7;
  font-size: 40px;
  font-size: calc(18px + (40 - 18) * ((100vw - 1024px) / (1440 - 1024)));
  letter-spacing: 0;
  line-height: 1.25;
  transition: border-color .3s;
  border-bottom: 1px solid transparent;
}

@media (max-width: 1024px) {
  .popup__email {
    font-size: 18px;
  }
}

@media (min-width: 1440px) {
  .popup__email {
    font-size: 40px;
  }
}

.no-touch .popup__email:hover {
  border-color: currentColor;
}

.popup__form {
  margin: 100px auto 0;
  margin-top: 80px;
  margin-top: calc(20px + (80 - 20) * ((100vw - 1024px) / (1440 - 1024)));
  max-width: 372px;
}

@media (max-width: 1024px) {
  .popup__form {
    margin-top: 20px;
  }
}

@media (min-width: 1440px) {
  .popup__form {
    margin-top: 80px;
  }
}

.popup__form .form-control:not(:first-child) {
  margin-top: 35px;
  margin-top: calc(10px + (35 - 10) * ((100vw - 1024px) / (1440 - 1024)));
}

@media (max-width: 1024px) {
  .popup__form .form-control:not(:first-child) {
    margin-top: 10px;
  }
}

@media (min-width: 1440px) {
